<template>
  <div class="admin-dashboard">
    <!-- Â§¥ÈÉ®Âå∫Âüü -->
    <div class="dashboard-header">
      <div class="header-content">
        <div>
          <h1 class="welcome-title">Ê¨¢ËøéÂõûÊù•Ôºå{{ user.real_name }}ÔºÅüëã</h1>

        </div>
        <el-button type="danger" plain @click="handleLogout" class="logout-btn" icon="el-icon-switch-button">
          ÈÄÄÂá∫ÁôªÂΩï
        </el-button>
      </div>
      <p class="welcome-sub">‰ªäÊó•Êúâ {{ pendingCount }} È°πÂæÖÂ§ÑÁêÜ‰∫ãÂä°</p>
    </div>

    <!-- Êï∞ÊçÆÊ¶ÇËßàÂç°Áâá -->
    <el-row :gutter="24" class="metric-grid">
      <el-col :xs="24" :sm="12" :lg="8">
        <el-card class="metric-card metric-pending" shadow="hover">
          <div class="metric-content">
            <div class="metric-icon">
              <i class="el-icon-time"></i>
            </div>
            <div class="metric-info">
              <span class="metric-label">ÂæÖÂÆ°Ê†∏</span>
              <span class="metric-value">{{ pendingCount }}</span>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="12" :lg="8">
        <el-card class="metric-card metric-active" shadow="hover">
          <div class="metric-content">
            <div class="metric-icon">
              <i class="el-icon-loading"></i>
            </div>
            <div class="metric-info">
              <span class="metric-label">ËøõË°å‰∏≠</span>
              <span class="metric-value">{{ activeCount }}</span>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="12" :lg="8">
        <el-card class="metric-card metric-completed" shadow="hover">
          <div class="metric-content">
            <div class="metric-icon">
              <i class="el-icon-success"></i>
            </div>
            <div class="metric-info">
              <span class="metric-label">Â∑≤ÂÆåÊàê</span>
              <span class="metric-value">{{ completedCount }}</span>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <!-- Êï∞ÊçÆË°®Ê†ºÂå∫Âüü -->
    <el-row :gutter="24" class="data-grid">
      <el-col :xs="24" :lg="12">
        <el-card class="data-card" shadow="never">
          <template #header>
            <div class="card-header">
              <div class="header-title">
                <i class="el-icon-box"></i>
                <span>ÊúÄÊñ∞Â§±Áâ©‰ø°ÊÅØ</span>
              </div>
              <el-button type="text" class="view-more">
              </el-button>
            </div>
          </template>

          <el-table :data="recentPosts" @row-click="handleRowClick" class="data-table" empty-text="ÊöÇÊó†ÂæÖÂ§ÑÁêÜ‰ø°ÊÅØ"
            v-loading="loading.posts" :header-cell-style="{ background: '#f8f9fa' }" height="330">
            <!--Ê∑ªÂä†Á©∫ÊèíÊßΩ-->
            <template #empty>
              <div class="empty-state">
                <i class="el-icon-document-remove"></i>
                <span>ÂΩìÂâçÊ≤°ÊúâÂèØÊòæÁ§∫ÁöÑÂ§±Áâ©‰ø°ÊÅØ</span>
              </div>
            </template>
            <el-table-column prop="title" label="Áâ©ÂìÅÂêçÁß∞" min-width="180">
              <template #default="{ row }">
                <span class="text-ellipsis">{{ row.title }}</span>
              </template>
            </el-table-column>
            <el-table-column label="Á±ªÂûã" width="100">
              <template #default="{ row }">
                {{ getItemTypeLabel(row.item_type) }}
              </template>
            </el-table-column>
            <el-table-column prop="category" label="ÂàÜÁ±ª" width="120">
              <template #default="{ row }">
                <el-tag effect="plain">{{ row.category }}</el-tag>
              </template>
            </el-table-column>
            <el-table-column label="Áä∂ÊÄÅ" width="100" align="center">
              <template #default="{ row }">
                <el-tag :type="statusTypeMap[row.status]" effect="light" class="status-tag">
                  {{ row.status }}
                </el-tag>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
      <el-col :xs="24" :lg="12">
        <el-card class="data-card" shadow="never">
          <template #header>
            <div class="card-header">
              <div class="header-title">
                <i class="el-icon-user"></i>
                <span>Êñ∞Â¢ûÁî®Êà∑</span>
              </div>
              <el-button type="text" class="view-more">
              </el-button>
            </div>
          </template>
          <el-table :data="recentUsers" @row-click="handleUserRowClick" class="data-table" v-loading="loading.users"
            :header-cell-style="{ background: '#f8f9fa' }" height="330">
            <el-table-column prop="username" label="Â≠¶Â∑•Âè∑" min-width="120">
              <template #default="{ row }">
                <div class="user-cell">
                  <el-avatar :size="24" :src="row.avatar || defaultAvatar">
                    {{ row.real_name?.charAt(0) || '?' }}
                  </el-avatar>
                  <span>{{ row.username || 'Êó†Êï∞ÊçÆ' }}</span>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="real_name" label="ÂßìÂêç" width="100" />
            <el-table-column prop="role" label="ËßíËâ≤" width="100">
              <template #default="{ row }">
                <el-tag :type="roleTagType(row.role)" effect="light" class="role-tag">
                  {{ row.role }}
                </el-tag>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
    </el-row>

    <!-- Áâ©ÂìÅËØ¶ÊÉÖÂºπÁ™ó -->
    <el-dialog title="üì¶ Áâ©ÂìÅËØ¶ÊÉÖ" :visible.sync="itemDialogVisible" width="800px" class="admin-detail-dialog">
      <el-row :gutter="20">
        <!-- Ê∑ªÂä†ÂõæÁâáËΩÆÊí≠Âå∫ -->
        <el-col :span="8">
          <el-carousel :interval="5000" height="300px" arrow="always">
            <el-carousel-item v-for="(img, index) in currentItem.images" :key="index">
              <el-image :src="img" :preview-src-list="currentItem.images" fit="cover" class="detail-image">
                <div slot="error" class="image-error">
                  <i class="el-icon-picture-outline"></i>
                </div>
              </el-image>
            </el-carousel-item>
          </el-carousel>
        </el-col>

        <!-- Ë∞ÉÊï¥‰ø°ÊÅØÂ±ïÁ§∫Âå∫ -->
        <el-col :span="16">
          <el-descriptions :column="2" border label-class-name="detail-label">
            <el-descriptions-item label="Áâ©ÂìÅÂêçÁß∞">{{ currentItem.title }}</el-descriptions-item>
            <el-descriptions-item label="ÂàÜÁ±ª">{{ currentItem.category }}</el-descriptions-item>
            <el-descriptions-item label="ÂèëÂ∏ÉÁ±ªÂûã">
              {{ currentItem.item_type === 'lost' ? 'Â§±Áâ©ÁôªËÆ∞' : 'ÊãõÈ¢ÜÁôªËÆ∞' }}
            </el-descriptions-item>

            <el-descriptions-item label="‰∏¢Â§±Êó∂Èó¥">{{ formatTime(currentItem.lost_time) }}</el-descriptions-item>
            <el-descriptions-item label="‰∏¢Â§±Âú∞ÁÇπ">
              {{ currentItem.location }}
              <!-- Ê∑ªÂä†Âú∞ÂõæÂÆπÂô® -->
              <div v-if="currentItem.location_lat && currentItem.location_lng" class="detail-map-container"
                :id="'detail-map-' + currentItem.id"></div>
            </el-descriptions-item>
            <el-descriptions-item label="Êèê‰∫§‰∫∫">
              <el-tooltip v-if="currentItem.user?.role === 'admin'" content="ÁÆ°ÁêÜÂëòË¥¶Âè∑">
                <i class="el-icon-s-custom"></i>
              </el-tooltip>
              {{ currentItem.user?.real_name || 'ÂåøÂêçÁî®Êà∑' }}
              <span v-if="currentItem.user" class="user-role-tag">
                ({{ roleMap[currentItem.user.role] }})
              </span>
            </el-descriptions-item>
            <el-descriptions-item label="Áä∂ÊÄÅ">
              <el-tag :type="statusTypeMap[currentItem.status]">{{ currentItem.status }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="ËÅîÁ≥ªÊñπÂºè" :span="2">
              <el-link type="primary">{{ currentItem.contact }}</el-link>
            </el-descriptions-item>
            <el-descriptions-item label="ËØ¶ÁªÜÊèèËø∞" :span="2">
              <pre class="description-pre">{{ currentItem.description }}</pre>
            </el-descriptions-item>
          </el-descriptions>
        </el-col>
      </el-row>

      <!-- Ê∑ªÂä†Â∫ïÈÉ®Êìç‰ΩúÊåâÈíÆ -->
      <div slot="footer">
        <el-button @click="itemDialogVisible = false">ÂÖ≥Èó≠</el-button>
        <el-button v-if="currentItem.status === 'pending'" type="success"
          :disabled="!currentItem.id || approvalProcessing" @click="handleApproveItem" :loading="approvalProcessing">
          {{ approvalProcessing ? 'Ê≠£Âú®Â§ÑÁêÜ...' : 'ÂÆ°Ê†∏ÈÄöËøá' }}
        </el-button>
        <!-- Ê∑ªÂä†Âà†Èô§ÊåâÈíÆ -->
        <el-button type="danger" :disabled="!currentItem.id || deletionProcessing" @click="handleDeleteItem"
          :loading="deletionProcessing">
          {{ deletionProcessing ? 'Ê≠£Âú®Âà†Èô§...' : 'Âà†Èô§Áâ©ÂìÅ' }}
        </el-button>
      </div>
    </el-dialog>


    <!-- Áî®Êà∑ËØ¶ÊÉÖÂºπÁ™ó -->
    <el-dialog title="üë§ Áî®Êà∑ËØ¶ÊÉÖ" :visible.sync="userDialogVisible" width="600px" class="admin-detail-dialog">
      <el-descriptions :column="2" border>
        <el-descriptions-item label="Áî®Êà∑ID">{{ currentUser.id }}</el-descriptions-item>
        <el-descriptions-item label="Áî®Êà∑Âêç">{{ currentUser.username }}</el-descriptions-item>
        <el-descriptions-item label="ÁúüÂÆûÂßìÂêç">{{ currentUser.real_name }}</el-descriptions-item>
        <el-descriptions-item label="Áî®Êà∑ËßíËâ≤">
          <el-tag :type="roleTagType(currentUser.role)">{{ currentUser.role }}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="Ê≥®ÂÜåÊó∂Èó¥">{{ currentUser.date_joined }}</el-descriptions-item>
        <el-descriptions-item label="ËÅîÁ≥ªÊñπÂºè">{{ currentUser.phone }}</el-descriptions-item>
        <el-descriptions-item label="ÁîµÂ≠êÈÇÆÁÆ±">{{ currentUser.email }}</el-descriptions-item>
      </el-descriptions>
    </el-dialog>

  </div>

</template>


<script>
import axios from 'axios';
import dayjs from "dayjs";

// import dayjs from 'dayjs';

export default {
  /* eslint-disable no-undef */
  data() {
    return {
      user: {
        real_name: 'Âä†ËΩΩ‰∏≠...',
        avatar: require('@/assets/touxiang.jpg'),
        role: '',
        phone: ''
      },
      defaultAvatar: require('@/assets/touxiang.jpg'),
      pendingCount: 0,
      activeCount: 0,
      completedCount: 0,
      recentPosts: [],
      recentUsers: [],
      loading: {
        posts: true,
        users: true
      },
      statusTypeMap: {
        pending: 'warning',
        active: 'primary',
        completed: 'success',
        expired: 'info'
      },
      // Êñ∞Â¢ûÂºπÁ™óÊéßÂà∂Áä∂ÊÄÅ
      itemDialogVisible: false,
      userDialogVisible: false,
      currentItem: {},
      currentUser: {},
      deletionProcessing: false, // Êñ∞Â¢ûÔºöÂà†Èô§Êìç‰ΩúÂ§ÑÁêÜÁä∂ÊÄÅ

    };
  },
  computed: {
    roleMap() {
      return {
        admin: 'ÁÆ°ÁêÜÂëò',
        teacher: 'ÊïôËÅåÂ∑•',
        student: 'Â≠¶Áîü'
      }
    }

  },
  mounted() {
    this.fetchAdminData();
    this.$nextTick(() => {
      const observer = new MutationObserver(() => {
        const loadingElements = document.querySelectorAll('.el-loading-mask');
        loadingElements.forEach(el => {
          if (el.style.display !== 'none') {
            setTimeout(() => el.style.display = 'none', 5000); // ÂºÇÂ∏∏ÊÉÖÂÜµ‰∏ãÊúÄÂ§öÂ±ïÁ§∫5Áßí
          }
        });
      });

      observer.observe(this.$el, {
        childList: true,
        subtree: true
      });
    });
    if (!window.AMap) {
      const key = 'db70318a1cf1f196b2746f10cb9df826'
      const plugins = [
        'AMap.Scale',
        'AMap.ToolBar'
      ].join(',')
      const script = document.createElement('script')
      script.src = `https://webapi.amap.com/maps?v=2.0&key=${key}&plugin=${plugins}`
      script.onerror = () => {
        console.error('È´òÂæ∑Âú∞ÂõæSDKÂä†ËΩΩÂ§±Ë¥•')
      }
      document.head.appendChild(script)
    }
  },
  methods: {
    // Â§ÑÁêÜÂà†Èô§Áâ©ÂìÅÁöÑÊñπÊ≥ï
    handleDeleteItem() {
      // Á°ÆËÆ§Áî®Êà∑ÊòØÂê¶ÁúüÁöÑË¶ÅÂà†Èô§Áâ©ÂìÅ
      this.$confirm('Á°ÆÂÆöË¶ÅÂà†Èô§ËØ•Áâ©ÂìÅÂêóÔºü', 'ÊèêÁ§∫', {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning'
      }).then(() => {
        this.deletionProcessing = true;
        // ÂèëÈÄÅÂà†Èô§ËØ∑Ê±Ç
        axios.delete(`/api/admin/items/${this.currentItem.id}/delete/`)
          .then(() => {
            this.deletionProcessing = false;
            // Âà†Èô§ÊàêÂäüÂêéÔºåÊõ¥Êñ∞È°µÈù¢Êï∞ÊçÆ
            this.recentPosts = this.recentPosts.filter(item => item.id !== this.currentItem.id);
            this.itemDialogVisible = false;
            this.$message.success('Áâ©ÂìÅÂà†Èô§ÊàêÂäü');
          })
          .catch(error => {
            this.deletionProcessing = false;
            this.$message.error('Áâ©ÂìÅÂà†Èô§Â§±Ë¥•Ôºö' + error.message);
          });
      }).catch(() => {
        // Áî®Êà∑ÂèñÊ∂àÂà†Èô§Êìç‰Ωú
        this.$message.info('Âà†Èô§Êìç‰ΩúÂ∑≤ÂèñÊ∂à');
      });
    },
    // ÂàùÂßãÂåñËØ¶ÊÉÖÂú∞Âõæ
    // ‰øÆÊîπinitDetailMapÊñπÊ≥ï
    initDetailMap() {
      if (!window.AMap) {
        this.$message.warning('Âú∞ÂõæËµÑÊ∫êÊ≠£Âú®Âä†ËΩΩÔºåËØ∑Á®çÂÄô')
        return
      }

      // Á°Æ‰øùÊï∞ÊçÆÊúâÊïàÊÄß
      const lng = parseFloat(this.currentItem.location_lng)
      const lat = parseFloat(this.currentItem.location_lat)
      if (isNaN(lng) || isNaN(lat)) {
        console.warn('Êó†ÊïàÁöÑÁªèÁ∫¨Â∫¶Êï∞ÊçÆ', this.currentItem)
        return
      }

      // ‰ΩøÁî®nextTickÁ°Æ‰øùDOMÊõ¥Êñ∞
      this.$nextTick(() => {
        try {
          this.destroyDetailMap()

          const mapContainerId = `detail-map-${this.currentItem.id}`
          const mapContainer = document.getElementById(mapContainerId)
          if (!mapContainer) return

          // ÂàùÂßãÂåñÂú∞Âõæ
          this.detailMap = new AMap.Map(mapContainerId, {
            zoom: 17,
            center: new AMap.LngLat(lng, lat),
            resizeEnable: true,
            viewMode: '3D'
          })

          // Ê∑ªÂä†Êéß‰ª∂
          const tools = [
            new AMap.Scale(),
            new AMap.ToolBar({
              position: { bottom: '20px', right: '20px' }
            })
          ]
          tools.forEach(t => t.addTo(this.detailMap))

          // Ê∑ªÂä†Ê†áËÆ∞
          new AMap.Marker({
            position: new AMap.LngLat(lng, lat),
            content: '<div class="detail-marker">üìç</div>',
            map: this.detailMap
          })
        } catch (e) {
          console.error('Âú∞ÂõæÂàùÂßãÂåñÂ§±Ë¥•:', e)
          this.$message.error('Âú∞ÂõæÂä†ËΩΩÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•')
        }
      })
    },
    // ÈîÄÊØÅÂú∞Âõæ
    // ‰øÆÊîπdestroyDetailMapÊñπÊ≥ï
    destroyDetailMap() {
      if (this.detailMap) {
        try {
          // Ê∏ÖÈô§ÊâÄÊúâË¶ÜÁõñÁâ©
          this.detailMap.clearMap()
          // ÈîÄÊØÅÂú∞ÂõæÂÆû‰æã
          this.detailMap.destroy()
          // ÁßªÈô§DOMÂÖÉÁ¥†
          const container = this.detailMap.getContainer()
          if (container && container.parentNode) {
            container.parentNode.removeChild(container)
          }
        } catch (e) {
          console.warn('Âú∞ÂõæÈîÄÊØÅÂºÇÂ∏∏:', e.message)
        }
        this.detailMap = null
      }
    },
    getItemTypeLabel(type) {
      return type === 'lost' ? 'Â§±Áâ©' : 'ÊãõÈ¢Ü';
    },
    getCategoryName(categoryId) {
      return axios.get(`/api/category/name/${categoryId}/`)
        .then(response => response.data.name)
        .catch(() => 'Êú™Áü•ÂàÜÁ±ª');
    },
    formatTime(time) {
      return dayjs(time).format('YYYY-MM-DD HH:mm')
    },
    // ÈÄÄÂá∫ÁôªÂΩï
    handleLogout() {
      this.$confirm('Á°ÆÂÆöË¶ÅÈÄÄÂá∫Á≥ªÁªüÂêóÔºü', 'Êìç‰ΩúÁ°ÆËÆ§', {
        type: 'warning',
        confirmButtonText: 'Á°ÆËÆ§ÈÄÄÂá∫',
        cancelButtonText: 'ÂèñÊ∂à'
      }).then(() => {
        this.$store.dispatch('logout')
        this.$router.replace('/login')
        this.$message.success('Â∑≤ÂÆâÂÖ®ÈÄÄÂá∫Á≥ªÁªü')
      }).catch(() => {
      })
    },
    roleTagType(role) {
      const roleTypes = {
        admin: 'danger',
        teacher: 'warning',
        student: 'success'
      };
      return roleTypes[role] || 'info';
    },
    async fetchAdminData() {
      try {
        this.loading = { posts: true, users: true } // ÈáçÁΩÆÂä†ËΩΩÁä∂ÊÄÅ
        const config = {
          headers: { Authorization: `Token ${localStorage.getItem('token')}` }
        };
        // Âπ∂Ë°åËØ∑Ê±Ç‰ºòÂåñ
        const [userResp, statsResp, postsResp, usersResp] = await Promise.all([
          axios.get('/api/user/profile/', config),
          axios.get('/api/admin/stats/', config),
          axios.get('/api/admin/recent-posts/', config),
          axios.get('/api/admin/recent-users/', config)
        ]);
        // Êï∞ÊçÆËµãÂÄº
        this.user = userResp.data;
        this.pendingCount = statsResp.data.pending_count;
        this.activeCount = statsResp.data.active_count;
        this.completedCount = statsResp.data.completed_count;
        this.recentPosts = postsResp.data.recent_posts;
        this.recentUsers = usersResp.data.recent_users;
      } catch (error) {
        console.error('Error:', error);
        this.$message.error(error.response?.data?.message || 'Êï∞ÊçÆÂä†ËΩΩÂ§±Ë¥•');
      } finally { // Á°Æ‰øùÊúÄÁªàÊ∏ÖÈô§Âä†ËΩΩÁä∂ÊÄÅ
        this.loading = { posts: false, users: false };
      }
    },
    // Áâ©ÂìÅËØ¶ÊÉÖ
    async handleRowClick(row) {
      try {
        const apiUrl = `/api/admin/found-items/${row.id}/`;
        const response = await axios.get(apiUrl, {
          headers: { Authorization: `Token ${localStorage.getItem('token')}` }
        });
        // Êñ∞Â¢ûÔºöËé∑ÂèñÂàÜÁ±ªÂêçÁß∞Âπ∂ÂêàÂπ∂Âà∞Êï∞ÊçÆ
        const categoryName = await this.getCategoryName(response.data.category);
        this.currentItem = {
          ...response.data,
          category: categoryName,  // Áî®ÂàÜÁ±ªÂêçÁß∞ÊõøÊç¢ÂéüÂßãIDÂÄº
          images: response.data.images || [],
          user: response.data.user || {}
        };
        this.itemDialogVisible = true;
      } catch (error) {
        this.$message.error('Ëé∑ÂèñËØ¶ÊÉÖÂ§±Ë¥•');
      }
    },
    // Áî®Êà∑ËØ¶ÊÉÖÂ±ïÁ§∫
    async handleUserRowClick(row) {
      try {
        const response = await axios.get(`/api/admin/users/${row.id}/`, {
          headers: { Authorization: `Token ${localStorage.getItem('token')}` }
        });
        this.currentUser = response.data;
        this.userDialogVisible = true;

        // Â§ÑÁêÜÊó•ÊúüÊ†ºÂºè
        this.currentUser.date_joined = new Date(this.currentUser.date_joined)
          .toLocaleString();
      } catch (error) {
        this.$message.error('Ëé∑ÂèñÁî®Êà∑ËØ¶ÊÉÖÂ§±Ë¥•');
        console.error('Error fetching user details:', error);
      }
    },
    // ÂÆ°Ê†∏ÈÄöËøá
    async handleApproveItem() {
      try {
        // Âº∫Ê†°È™åÁâ©ÂìÅÁä∂ÊÄÅ
        if (this.currentItem.status !== 'pending') {
          throw new Error('Âè™ËÉΩÂÆ°Ê†∏ÂæÖÂ§ÑÁêÜÁä∂ÊÄÅÁöÑÁâ©ÂìÅ')
        }
        await this.$confirm('Á°ÆÂÆöË¶ÅÈÄöËøáËØ•Áâ©ÂìÅÁöÑÂÆ°Ê†∏ÂêóÔºü', 'Êìç‰ΩúÁ°ÆËÆ§', {
          confirmButtonClass: 'el-button--danger' // ‰ΩøÁî®Âç±Èô©ÊåâÈíÆÊ†∑Âºè
        });
        const startTime = Date.now()
        await this.$http.patch(
          `/admin/items/${this.currentItem.id}/status/`, // ‰ΩøÁî®‰∏ìÁî®Áä∂ÊÄÅÊé•Âè£
          {
            status: 'active',
            admin_remark: 'Â∑≤ÈÄöËøáÂÆ°Ê†∏' // Ê∑ªÂä†ÂÆ°Ê†∏Â§áÊ≥®
          },
          {
            headers: {
              'X-Request-ID': `approve-req-${this.currentItem.id}-${Date.now()}`, // ÂîØ‰∏ÄËØ∑Ê±ÇÊ†áËØÜ
              'Content-Type': 'application/json-patch+json' // Ê†áÂáÜPATCHÁ±ªÂûã
            }
          }
        )
        // ÊâßË°åÊú¨Âú∞Êï∞ÊçÆÊõ¥Êñ∞
        this.currentItem = {
          ...this.currentItem,
          status: 'active',
          admin_remark: 'Â∑≤ÈÄöËøáÂÆ°Ê†∏'
        }
        // Êõ¥Êñ∞ÁªüËÆ°Êï∞Â≠ó
        this.pendingCount--
        this.activeCount++
        // Êõ¥Êñ∞Ë°®Ê†ºÊï∞ÊçÆ
        this.recentPosts = this.recentPosts.map(item =>
          item.id === this.currentItem.id ?
            { ...item, status: 'active' } :
            item
        )
        console.log(`ÂÆ°Ê†∏Êìç‰ΩúËÄóÊó∂ ${Date.now() - startTime}ms`)
        this.$message.success(`${this.currentItem.title} ÂÆ°Ê†∏ÈÄöËøá`)
      } catch (error) {
        const isCancel = error === 'cancel'
        const msg = isCancel ? 'Êìç‰ΩúÂ∑≤ÂèñÊ∂à' :
          error.response?.data?.error || `ÂÆ°Ê†∏Â§±Ë¥•: ${error.message}`

        !isCancel && console.error('ÂÆ°Ê†∏ÈîôËØØËØ¶ÊÉÖ:', {
          error: error.response?.data,
          item: this.currentItem
        })

        this.$message[isCancel ? 'info' : 'error'](msg)
      }
    },
  },
  // Ê∑ªÂä†watchÁõëÂê¨ÂØπËØùÊ°ÜÁä∂ÊÄÅ
  watch: {
    itemDialogVisible(newVal) {
      if (newVal) {
        // Ê∑ªÂä†Âª∂ËøüÁ°Æ‰øùDOMÊ∏≤ÊüìÂÆåÊàê
        setTimeout(() => {
          this.initDetailMap()
        }, 300)
      } else {
        this.destroyDetailMap()
      }
    },
    // ÁõëÂê¨ÂΩìÂâçÁâ©ÂìÅÂèòÂåñ
    currentItem: {
      deep: true,
      handler() {
        if (this.itemDialogVisible) {
          this.$nextTick(() => {
            this.initDetailMap()
          })
        }
      }
    }
  }
};
</script>

